.header {
    /* background-color: #365587; */
    background-color: #061A3B;;
    width: 100%;
    border-bottom: 1px white solid;
}

.header_content {
    width: 1800px;
    text-align: left;
    margin: auto;
    display: flex;
    justify-content: space-between;
    height: 65px;
}

.header_title {
    height: 100%;
    display: flex;
}

.header_content img {
    max-height: 95%;
    width: auto;
    display: inline-block;
    margin-left: 15px;
}

.scoreboard {
    margin-right: 15px;
}

.header_content .score {
    height: 100%;
    background-color: #365587;
    width: 200px;
    text-align: center;
    border-left: 1px solid white;
    border-right: 1px solid white;
    color: #D4C13F;
    display: inline-block;
    font-family: Arial, Helvetica, sans-serif;
}

.score_header {
    font-weight: bold;
    font-size: 1.2em;
    margin-top: 5px;
    margin-bottom: 10px;
    display: block;
}