.sidebar {
    border-radius: 25px;
    background-color: #365587;
    width: 450px;
    float: left;
    height: 870px;
    overflow: none;
    padding-bottom: 10px;
}

.sidebar h2 {
    text-align: center;
    color: #D4C13F;
}

ul#gamePicker {
    list-style: none;
    text-align: left;
    padding: 0px 10px 0px 10px;
}

ul#gamePicker li {
    padding: 5px;
    padding-left: 10px;
    border-radius: 10px;
}

ul#gamePicker .selected {
    color: #D4C13F;
    background-color: #061A3B;
}

ul#gamePicker li:hover {
    cursor: pointer;
    color: #D4C13F;
    background-color: #061A3B;
}

.buttonCenter {
    width: 100%;
    text-align: center;
}

.startGame {
    border-radius: 20px;
    padding: 5px;
}

.gameScores {
    padding: 10px 0px 10px 5px;
    text-align: center;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
}

.gameScores span {
    width: 50%;
    display: inline-block;
}

.actions {
    height: 375px;
}

.actions form {
    margin-left: 5px;
    margin-right: 5px;
}

.log {
    height: 375px;
    border-bottom: 1px solid gray;
    overflow: auto;
    margin-bottom: 10px;
}