.GameNight {
    /* background-color: #061A3B; */
    background-color: #222;
    min-height: 100vh;
    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.body {
    padding-top: 1vh;
    width: 1800px;
    margin: auto;
    border-radius: 25px;
}

.body:after{
    display: table;
    clear: both;
}

.board {
    width: 1350px;
    float: left;
    text-align: right;
}

.GameNight p {
    text-align: left
}

iframe#gameBoard {
    border-radius: 25px;
    height: 872px;
    width: 1325px;
}

.footer {
    text-align: right;
    font-size: .9em;
}